.pb-click-to-tweet-wrapper {
	transition: all 0.3s ease-in-out;
	position: relative;
	.pb-click-to-tweet--button {
		display: inline-block;
		cursor: pointer;
		text-decoration: none !important;
		&:before {
			content: " ";
			background: url(data:image/svg+xml;base64,);
			background-size: cover;
			width: 20px;
			display: inline-block;
			height: 18px;
			position: relative;
			top: 4px;
			margin-right: 5px;
		}
	}
}